#root {
  width: 100%;
  color: #666;
  background: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

button {
  padding: 0.5rem 0.8rem;
  border: none;
  cursor: pointer;
}
